<template>
  <div id="app">
    <div v-if='page.length'>
      <div class='title'>{{page[index].title}}</div>
      <Question
      v-for="(item,serial) in page[index].list"
      :question='item'
      :last='serial == page[index].list.length-1'
      @addAnswer="addAnswer"></Question>
      <Button type="primary" size="large"
      v-if='index !== page.length-1'
      @click='next'>
        Next
        <Icon type="ios-arrow-forward" />
      </Button>
      <Button type="primary" size="large"
       v-if='index == page.length-1'
      @click='next'>
        Submit
        <Icon type="ios-arrow-forward" />
      </Button>
    </div>
    <Spin size="large" v-if='page.length==0' class='spin'></Spin>
    <BackTop></BackTop>
  </div>
</template>

<script>
import Question from "./components/question.vue";
import Service from "./services/service.js";

export default {
  name: "app",
  components: {
    Question
  },
  data: function() {
    return {
      index: 0,
      page: [],
      answer: {
        random: "",
        userID: "",
        score: "",
        list: []
      }
    };
  },
  mounted: function() {
    /**
     * random  0123  
     */
    setTimeout(() => {
      this.getQuestion();
    }, 100);
  },
  methods: {
    getQuestion: function() {
      let random = parseInt(Math.random() * 4);
      let random1 = parseInt(Math.random() * 3);
      this.answer.random = [random, random1];
      let q = Service.getQuestion(random, random1);
      let page = [];
      let index = 0;
      let json = {
        title: "Human Robot Trust Experiment",
        list: []
      };
      q.forEach(item => {
        if (item.q.type !== 5) {
          if (item.q.type == 3) {
            json.list.push(item);
          }
        } else {
          if (json.list.length) {
            page[index] = json;
            index++;
          }
          json = {
            title: item.q.title,
            list: []
          };
        }
      });
      this.page = page;
      console.log(this.page);
    },
    addAnswer: function(answer) {
      let exist = false;
      this.answer.list.forEach((item, index) => {
        if (item.id == answer.id) {
          this.answer.list[index] = answer;
          exist = true;
        }
      });
      if (!exist) {
        this.answer.list.push(answer);
      }
      console.log(this.answer.list[0]);
    },
    next: function() {
      /**
       * 
       */
      let check = this.checkAnswer();
      if (!check) {
        this.$Message.error({
          content: ""
        });
        return;
      }
      if (this.index == this.page.length - 1) {
        /**
         * 
         */
        this.getAllScore();
        this.submit();
      } else {
        this.index++;
      }
    },
    checkAnswer: function() {
      let res = false;
      let length = this.answer.list.length;
      let topicLength = 0;
      this.page.forEach((item, index) => {
        if (index <= this.index) {
          item.list.forEach(child => {
            let type = child.q.type;
            if (type == 1 || type == 2 || type == 3) {
              topicLength++;
            }
          });
        }
      });
      if (length == topicLength) {
        res = true;
      }
      return res;
    },
    getAllScore: function() {
      console.log(this.answer.list);
    },
    submit: function() {
      this.$http({
        method: "get",
        url: "http://127.0.0.1:7001/api/submit",
        data: {
          name: "virus"
        }
      }).then((data)=>{
        debugger
      })
    }
  }
};
</script>

